const languageValidationMap = {
  required: {
    en: 'Mandatory field',
    es: 'Campo obligatorio',
    pt: 'Campo obrigatório'
  },
  invalidPhone: {
    en: 'Invalid phone number',
    es: 'Número de teléfono no válido',
    pt: 'Número de telefone inválido'
  },
  invalidEmail: {
    en: 'The e-mail entered is wrong',
    es: 'El correo electrónico escrito contiene un error',
    pt: 'O e-mail digitado contém algum erro'
  },
};

const WorkWithUsForm = ({ language = 'pt' }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            recaptchaSiteKey
          }
        }
      }
    `
  );
  const { recaptchaSiteKey } = site.siteMetadata;

  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);
  const [serverErrorMessage, setServerErrorMessage] = useState();
  const [recaptchaToken, setRecaptchaToken] = useState();
  const [isMobile] = useState(useIsMobile());

  useEffect(() => {
    !isMobile && loadReCaptcha(recaptchaSiteKey)
  }, [recaptchaSiteKey, isMobile]);

  const handleSubmit = async (values, actions) => {
    try {
      await axios.post('https://celera-backend.herokuapp.com/contact-work', {
        ...values,
        locale: language
      }, {
        headers: {
          Authorization: recaptchaToken,
        }
      })
      setSuccess(true);
      Tracking.trackWorkWithUsFormSubmitted();
    } catch (err) {
      if (err.response.status === 400) {
        setServerErrorMessage(err.response.data.message);
      }
      actions.setSubmitting(false);
      setError(true);
    }
  };

  const recaptchaVerifyCallback = (token) => setRecaptchaToken(token);

  const validationSchema = Yup.object({
    name: Yup.string().required(languageValidationMap.required[language]),
    phone: Yup.string().matches(/^\(\d{2}\)[ ]\d{4,5}-\d{4}$/, languageValidationMap.invalidPhone[language]),
    email: Yup.string().email(languageValidationMap.invalidEmail[language]),
    type: Yup.string().required(languageValidationMap.required[language]),
    nativeLanguage: Yup.string().required(languageValidationMap.required[language]),
    firstLanguagePair: Yup.string().required(languageValidationMap.required[language]),
    isCompany: Yup.string().required(languageValidationMap.required[language]),
    translationMemory: Yup.string().required(languageValidationMap.required[language]),
    hasTrados: Yup.string().required(languageValidationMap.required[language]),
    dailyProductivity: Yup.string().required(languageValidationMap.required[language]),
    costPerWord: Yup.string().required(languageValidationMap.required[language]),
    firstExperience: Yup.string().required(languageValidationMap.required[language]),
    secondExperience: Yup.string().required(languageValidationMap.required[language]),
    thirdExperience: Yup.string().required(languageValidationMap.required[language]),
    firstFormation: Yup.string().required(languageValidationMap.required[language]),
  });

  const availableProductivity = range(1, 10).map(p => `${p}.000`);
  const availableCostPerWord = range(1, 21).map(cost => `R$ 0.${cost < 10 ? '0' + cost : cost}`);
  const availableLanguagesPair = {
    en: [
      'English-Portuguese',
      'Portuguese-English',
      'Spanish-Portuguese',
      'Portuguese-Spanish',
      'Spanish-English',
      'English-Spanish',
      'Other language combination'
    ],
    es: [
      'Inglés-Portugués',
      'Portugués-Inglés',
      'Español-Portugués',
      'Portugués-Español',
      'Español-Inglés',
      'Inglés-Español',
      'Otra combinación de idiomas'
    ],
    pt: [
      'Inglês-Português',
      'Português-Inglês',
      'Espanhol-Português',
      'Português-Espanhol',
      'Espanhol-Inglês',
      'Inglês-Espanhol',
      'Outra combinação de idiomas'
    ]
  };

  const maskedInput = ({ field }) =>
    <MaskedInput
      {...field}
      mask={value => {
        const numbers = value.match(/\d/g)
        const numberLength = numbers ? numbers.join('').length : 0

        return ['(', /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, /\d/, ...(numberLength > 10 ? [/\d/] : []), '-', /\d/, /\d/, /\d/, /\d/]
      }}
      guide={false}
      placeholder='(DDD)'
    />

  return (
    <div class='workwithus-form--wrapper'>
      {success && 
        <div class='workwithus-form--success'>
          <h3>{languageMap.successThanks[language]}</h3>
          <p>{languageMap.sucessEmail[language]}</p>
        </div>
      }
      {!success &&
        <>
          <div class='workwithus-form--headline'>
            <p>{languageMap.descriptionText[language]}</p>
          </div>
          <div>
            <Formik
                  initialValues={{}}
                  validationSchema={validationSchema}
                  onSubmit={handleSubmit}
            >
              {({ isValid, isSubmitting }) => (
                <Form class='workwithus-form--inputs-wrapper'>

                  <div class='workwithus-form--section'>
                    <h3>{languageMap.basicInfoTitle[language]}</h3>
                    <p>{languageMap.basicInfoDescription[language]}</p>

                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.type[language]}</span>
                        <div class='workwithus-form--input-checkbox'>
                          <label>
                            <Field type="checkbox" name="type" value="revisor"/>
                            <span>{languageMap.reviewer[language]}</span>
                          </label>
                          <label>
                            <Field type="checkbox" name="type" value="tradutor"/>
                            <span>{languageMap.translator[language]}</span>
                          </label>
                        </div>
                        <ErrorMessage name="type" component="div" />
                      </div>
                    </div>
                    <div class='workwithus-form--divider' />
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.name[language]}</span>
                        <Field type="text" name="name"/>
                        <ErrorMessage name="name" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.phone[language]}</span>
                        <Field type="text" name="phone" render={maskedInput}/>
                        <ErrorMessage name="phone" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.email[language]}</span>
                        <Field type="text" name="email"/>
                        <ErrorMessage name="email" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.linkedin[language]}</span>
                        <Field type="text" name="linkedin"/>
                        <ErrorMessage name="linkedin" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.website[language]}</span>
                        <Field type="text" name="website"/>
                        <ErrorMessage name="website" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.issueInvoices[language]}</span>
                        <div class='workwithus-form--input-radio'>
                          <label>
                            <Field type="radio" name="isCompany" value="sim"/>
                            <span>{languageMap.yes[language]}</span>
                          </label>
                          <label>
                            <Field type="radio" name="isCompany" value="nao"/>
                            <span>{languageMap.no[language]}</span>
                          </label>
                        </div>
                        <ErrorMessage name="isCompany" component="div" />
                      </div>
                    </div>
                  </div>

                  <div class='workwithus-form--section'>
                    <h3>{languageMap.languageTitle[language]}</h3>
                    <p>{languageMap.languageDescription[language]}</p>

                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.languageNative[language]}</span>
                        <Field type="text" name="nativeLanguage"/>
                        <ErrorMessage name="nativeLanguage" component="div" />
                      </div>
                    </div>
                    <div class="contact-form--inputs-group">
                      <div class="contact-form--input-wrapper">
                        <span>{languageMap.languagePair[language]}</span>
                        <Field component="select" name="firstLanguagePair" defaultValue="">
                          <option value="" disabled>{languageMap.select[language]}</option>
                          {map(availableLanguagesPair[language], (type) => <option value={type} key={type}>{type}</option>)}
                        </Field>
                      </div>
                      <div class="contact-form--input-wrapper">
                        <span>{languageMap.languageOtherPair[language]}</span>
                        <Field component="select" name="secondLanguagePair" defaultValue="">
                          <option value="" disabled>{languageMap.select[language]}</option>
                          {map(availableLanguagesPair[language], (language) => <option value={language} key={language}>{language}</option>)}
                        </Field>
                      </div>                
                    </div>
                    <div class="workwithus-form--input-wrapper">
                      <span>{languageMap.languageTrados[language]}</span>
                      <div class='workwithus-form--input-radio'>
                        <label>
                          <Field type="radio" name="hasTrados" value="sim"/>
                          <span>{languageMap.yes[language]}</span>
                        </label>
                        <label>
                          <Field type="radio" name="hasTrados" value="nao"/>
                          <span>{languageMap.no[language]}</span>
                        </label>
                      </div>
                      <ErrorMessage name="hasTrados" component="div" />
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.languageAnotherMemory[language]}</span>
                        <Field component="select" name="translationMemory" defaultValue="">
                          <option value="" disabled>{languageMap.select[language]}</option>
                          <option value={languageMap.no[language]} key={languageMap.no[language]}>{languageMap.no[language]}</option>
                          <option value={languageMap.yesMemory[language]} key={languageMap.yesMemory[language]}>{languageMap.yesMemory[language]}</option>
                        </Field>
                        <ErrorMessage name="translationMemory" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.languageProductivity[language]}</span>
                        <Field component="select" name="dailyProductivity" defaultValue="">
                          <option value="" disabled>{languageMap.select[language]}</option>
                          {map(availableProductivity, (dP) => <option value={dP} key={dP}>{dP}</option>)}
                        </Field>
                        <ErrorMessage name="dailyProductivity" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.languageCost[language]}</span>
                        <Field component="select" name="costPerWord" defaultValue="">
                          <option value="" disabled>{languageMap.select[language]}</option>
                          {map(availableCostPerWord, (cPw) => <option value={cPw} key={cPw}>{cPw}</option>)}
                        </Field>
                        <ErrorMessage name="costPerWord" component="div" />
                      </div>
                    </div>
                  </div>

                  <div class='workwithus-form--section'>
                    <h3>{languageMap.experienceTitle[language]}</h3>
                    <p>{languageMap.experienceDescription[language]}</p>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.experienceProfessional[language]} 1</span>
                        <Field type="text" name="firstExperience"/>
                        <ErrorMessage name="firstExperience" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.experienceProfessional[language]} 2</span>
                        <Field type="text" name="secondExperience"/>
                        <ErrorMessage name="secondExperience" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.experienceProfessional[language]} 3</span>
                        <Field type="text" name="thirdExperience"/>
                        <ErrorMessage name="thirdExperience" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.experienceProfessional[language]} 4 ({languageMap.optional[language]})</span>
                        <Field type="text" name="fourthExperience"/>
                        <ErrorMessage name="fourthExperience" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.experienceProfessional[language]} 5 ({languageMap.optional[language]})</span>
                        <Field type="text" name="fifthExperience"/>
                        <ErrorMessage name="fifthExperience" component="div" />
                      </div>
                    </div>
                  </div>
                  
                  <div class='workwithus-form--section'>
                    <h3>{languageMap.courseTitle[language]}</h3>
                    <p>{languageMap.courseDescription[language]}</p>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.courseLabel[language]} 1</span>
                        <Field type="text" name="firstFormation"/>
                        <ErrorMessage name="firstFormation" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.courseLabel[language]} 2 ({languageMap.optional[language]})</span>
                        <Field type="text" name="secondFormation"/>
                        <ErrorMessage name="secondFormation" component="div" />
                      </div>
                    </div>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.courseLabel[language]} 3 ({languageMap.optional[language]})</span>
                        <Field type="text" name="thirdFormation"/>
                        <ErrorMessage name="thirdFormation" component="div" />
                      </div>
                    </div>
                  </div>

                  <div class='workwithus-form--section'>
                    <h3>{languageMap.obsversationTitle[language]}</h3>
                    <p>{languageMap.obsversationDescription[language]}</p>
                    <div class="workwithus-form--inputs-group">
                      <div class="workwithus-form--input-wrapper">
                        <span>{languageMap.obsversationLabel[language]}</span>
                        <Field component="textarea" name="aditionalInfo"/>
                        <ErrorMessage name="aditionalInfo" component="div" />
                      </div>
                    </div>
                  </div>
                  <button type="submit" disabled={isSubmitting || !isValid || success} class={success ? '-success' : ''}>
                    {languageMap.send[language]}
                  </button>
                  <ReCaptcha
                    sitekey={recaptchaSiteKey}
                    action='contact_form'
                    verifyCallback={recaptchaVerifyCallback}
                  />
                </Form>
              )}
            </Formik>
          </div>
        </>
      }
    </div>
  );
}

export default WorkWithUsForm;
